<template>
	<view>
		<image mode="widthFix" style="width: 30rpx" :src="require('@/static/返回.png')"
		@click="back"/>
	</view>
</template>

<script>
	export default {
		name:"my-back-button",
		methods:{
			back(){
				this.$emit("doExtra")
				this.util.routeBack()//注意路由返回会导致页面的卸载
			}
		}
	}
</script>
